"use client"
import { products } from "@/lib/data"
import { Check } from "lucide-react";
import { useState } from "react";
import { FaStar, FaStarHalfAlt } from "react-icons/fa";

export async function generateStaticParams() {
    return products.map((product) => ({
        slug: product.id
    }))
}

export default function Page({ params }) {
    const [checkColor, setCheckColor] = useState(null)
    const product = products.find(p => p.id === params.slug)

    //rating
    const maxStars = 5;
    const rating = product.rating;

    const getStarIcon = (index) => {
        if (index + 1 <= Math.floor(rating)) {
            return <FaStar className="text-[#FFC633]" />;
        } else if (index < rating && rating % 1 >= 0.5) {
            return <FaStarHalfAlt className="text-[#FFC633]" />;
        } else {
            return null
        }
    };

    //check color

    return (
        <div className="flex h-screen">
            <div className="relative flex bg-amber-200 w-1/2 m-10 ">
                <div className="absolute right-0 w-[450px] h-[298px]">
                    <img src={product.pictures[0]} alt="mainpic" />
                </div>
                <div className="flex flex-col gap-3">
                    {product.pictures.map((ulr) => (
                        <img
                            className=" w-[152px]"
                            src={ulr} key={ulr} />)
                    )}
                </div>
            </div>
            <div className="m-10 w-1/2">
                <div>
                    <h1>{product.name}</h1>
                    <div className="flex items-center mt-1 gap-1">
                        {[...Array(maxStars)].map((_, i) => (
                            <span key={i}>
                                {getStarIcon(i)}
                            </span>
                        ))}
                        <span className="ml-1 text-sm">{product.rating}/5</span>
                    </div>
                    {product.discount === 0 ?
                        <p className="mt-1 font-bold text-lg">${product.price}</p> : (
                            <div className="flex items-center gap-2">
                                <p className="mt-1 font-bold text-lg">${(product.price * (1 - product.discount / 100)).toFixed(0)}</p>
                                <p className="mt-1 font-bold text-lg line-through text-gray-500">${product.price}</p>
                                <span className="text-sm text-[#FF3333] bg-[#ffebeb] py-1 px-3 rounded-2xl">-{product.discount}%</span>
                            </div>
                        )}
                    <p>{product.description}</p>
                </div>
                <hr />
                <div>
                    <h1>Select Colors</h1>
                    <div className="flex gap-2">
                        {product.color.map((color) => (
                            <div className="h-[37px] w-[37px] rounded-4xl items-center flex justify-center" onClick={() => setCheckColor(color)} key={color} style={{ backgroundColor: color }}>
                                {checkColor === color && (
                                    <Check className="text-white" />
                                )}
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    )

}